export default {
  props: {
    altTitle: {
      type: String,
      required: false,
    },
    title: {
      type: String,
      required: false,
    },
    subtitle: {
      type: String,
      required: false,
    },
    data: {
      type: String,
      required: false,
    },
    minDate: {
      type: String,
      required: false,
    },
    chartHeight: {
      type: Number,
      required: false,
    },
  },
  data() {
    return {
      chart: require(`~/json/confluence/${this.data}.json`),
      heatdata: "",
    };
  },
  created() {
    let modifiedArr = this.chart.chartdata.labels.map(function (element) {
      return element.slice(0, -6);
    });

    const obj = {};
    for (let i = 0; i < modifiedArr.length; i++) {
      obj[modifiedArr[i]] = [];
    }

    var currentNum = this.chart.chartdata.datasets[0].data
      .filter((val, index, arr) => (index = arr.length))
      .reverse();

    for (let i = 0; i < currentNum.length; i++) {
      var A = currentNum[i];
      var B = currentNum[i - 1];
      var C = (((B - A) / B) * 100.0).toFixed(2);

      obj[this.chart.chartdata.labels[i].slice(0, -6)].push(C);
    }
    this.heatdata = obj;
    var currentYear = Array(12).fill(0);

    for (let i in obj) {
      if (obj[i].length < 12) {
        const mergeResult = [...obj[i], ...currentYear];
        mergeResult.length = 12;
        obj[i] = mergeResult;
      }
    }
  },
};
